import React from "react";
import { Col, Row } from "react-bootstrap";
import { DiUbuntu } from "react-icons/di";
import {
  SiVisualstudiocode,
  SiPostman,
  SiHeroku,
  SiWebstorm,
} from "react-icons/si";

function Toolstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <Col xs={4} md={2} className="tech-icons">
        <DiUbuntu color="#212529" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiVisualstudiocode color="#212529" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiWebstorm color="#212529" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiPostman color="#212529" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiHeroku color="#212529" />
      </Col>
    </Row>
  );
}

export default Toolstack;
