import React from "react";
import Typewriter from "typewriter-effect";

function Type() {
  return (
    <Typewriter
      options={{
        strings: [
          "Full Stack Engineer",
          "JavaScript Developer",
          "MERN Stack Developer",
        ],
        autoStart: true,
        loop: true,
        pauseFor: 5000,
        deleteSpeed: 50,
      }}
    />
  );
}

export default Type;
