import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  DiJavascript1,
  DiReact,
  DiNodejs,
  DiMongodb,
  DiPython,
  DiGit,
  DiAngularSimple,
} from "react-icons/di";
import { FaVuejs, FaLaravel, FaPhp, FaAws, FaDocker } from "react-icons/fa";
import { SiFirebase, SiMysql, SiGooglecloud } from "react-icons/si";

function Techstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <Col xs={4} md={2} className="tech-icons">
        <DiJavascript1 color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiNodejs color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiReact color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiAngularSimple color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaVuejs color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaLaravel color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiPython color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaPhp color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiGit color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiMysql color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <DiMongodb color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiFirebase color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaAws color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiGooglecloud color="#263238" />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaDocker color="#263238" />
      </Col>
    </Row>
  );
}

export default Techstack;
