import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import myImg from "../../Assets/avatar.svg";
import Tilt from "react-parallax-tilt";
import { AiFillGithub } from "react-icons/ai";
import { FaLinkedinIn, FaStackOverflow } from "react-icons/fa";
import { user } from "../../constants";

function Introduce() {
  return (
    <Container fluid className="home-about-section" id="about">
      <Container>
        <Row>
          <Col md={4} className="myAvtar">
            <Tilt>
              <img src={myImg} className="img-fluid" alt="avatar" />
            </Tilt>
            <ul className="home-about-social-links">
              <li className="social-icons">
                <a
                  href={user.github}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <AiFillGithub />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href={user.stackoverflow}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <FaStackOverflow />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href={user.lin}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <FaLinkedinIn />
                </a>
              </li>
            </ul>
            {/* <a
              className="text-center purple"
              href={`mailto:${user.email}`}
              style={{ marginTop: 16, display: "block", fontSize: 20 }}
            >
              {user.email}
            </a> */}
          </Col>
          <Col md={8} className="home-about-description">
            <h1 className="project-heading text-center text-md-left">
              <strong className="purple">{user.name}</strong>
            </h1>
            <p className="home-about-body">
              I’m a full stack developer with <b className="purple">7+</b> years
              of experience.
              <br />
              <br />I fell in love with programming and I have at least learnt
              I've worked with some companies and on several industries in my
              career.
              <br />
              My main programming language is{" "}
              <b className="purple">JavaScript</b> and I have some experience in{" "}
              <b className="purple">PHP</b> and <b className="purple">Python</b>
              .
              <br />
              As a full stack developer, I can do both of frontend and backend.
              <br />
              I'm using JavaScript SPA frameworks such as{" "}
              <b className="purple">React</b>, <b className="purple">Angular</b>
              , <b className="purple">Vue</b> for the frontend and{" "}
              <b className="purple">Node</b> for the backend.
              <br />
              <br />
              Also, I'm a huge fan of serverless and cloud services like{" "}
              <b className="purple">AWS</b> and <b className="purple">GCP</b>.
              <br />
              <br />
              With my vast resources and rich experience, I can do any jobs.
              <br />
              Please contact me.
            </p>
          </Col>
        </Row>
        {/* <Row>
          <Col md={12} className="home-about-social">
            <h1>Find Me On</h1>
            <p>
              Feel free to <span className="purple">connect </span>with me
            </p>
            <ul className="home-about-social-links">
              <li className="social-icons">
                <a
                  href={user.github}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <AiFillGithub />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href={user.stackoverflow}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <FaStackOverflow />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href={user.lin}
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <FaLinkedinIn />
                </a>
              </li>
            </ul>
          </Col>
        </Row> */}
      </Container>
    </Container>
  );
}
export default Introduce;
